
#app {
    width: 80%;
    margin: 40px auto;
    font-family: Helvetica, Arial, sans-serif;
}
.view {
    float: right;
    width: 50%;
    margin: 0;
    font-family: Helvetica, Arial, sans-serif;
    font-size: 14px;
    line-height: 1.42857143;
    color: #333;
}
a {
    text-decoration: none;
}
a:hover {
    text-decoration: underline;
}
.text {
    display: inline-block;
    max-width: 45%;
    min-width: 45%;
    height: 500px;
    overflow: scroll;
    font-family: Helvetica, Arial, sans-serif;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
}



